body {
  margin: 0;
  padding: 30px 15px;
}
* {
  margin: 4px 2px;
}
button {
  cursor: pointer;
  margin: 5px;
}

.error {
  color: red;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
.success {
  color: green;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
